.users-div{
    margin: 15px auto;
    font-size: 14px;
    width: 98%;
    background-color: white;
    padding-bottom: 10px;
}

.no-user{

    clear: right;
    font-size: 20px;
    color: red ;
    width: 100%;
    padding: 8px 0px;
    margin-top: 80px;
    border-radius: 10px;
    text-align: center;
    background-color: rgb(191, 198, 198);
}

.card-container-header{
    
    padding: 15px 10px;
    display: flex;
    align-items: center;
    position: relative;
    p{
        display: inline-block;
        font-size: 20px;
        margin: 0 !important;
    }

    .userAddBtn{
        background-color: #FFFFFF;
        color: #F9B8A4;
        width: 140px;
        border: 2px solid #F9B8A4;
        font-weight: bold;
        outline: none;
        position: absolute;
        top: 6px;
        right: 10px;
        height: 40px;
    }

}

.user-card-container{
   
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 50px;

    .card{
        width: 32%;
        border: none;
        margin-right: 15px;
        // border: 1px solid rgba(184, 181, 181, 0.5);

        h5{
            font-size: 16px;
        }

        .card-body{
            padding: 0px !important;
            //padding: 10px 20px !important;
        }

        %style-icon{
            display: inline-block;
            border-radius: 50%;
            // border: 1px solid;
            padding: 3px 8px;
        }

        .card-value{
            padding-top: 4px;
        }

        .circle{
            position: absolute;
            //background-color: gray;
     

            .card-icon-all{
                @extend %style-icon;
                background-color: #7F66B2;
                // animation: animate 1.5s linear infinite;
            }

            .card-icon-all:hover{
                animation-play-state: paused;
                background-color: #8e7cb3;
            }

            

            .card-icon-active{
                @extend %style-icon;
                background-color: #30926F;
                // animation: animate1 1.5s linear infinite;
            }

            .card-icon-active:hover{
                animation-play-state: paused;
                background-color: #50927b;
            }

            

            .card-icon-inactive{
                @extend %style-icon;
                background-color: #BB6C57;
                // animation: animate2 1.5s linear infinite;
            }

            .card-icon-inactive:hover{
                animation-play-state: paused;
                background-color: #b97c6b;
            }

            
        }
        
        .card-icon{
            color: white;
            font-size: 14px;
            display: inline-block;
        }

        .card-value{
            // display: inline-block;
            float: right;
        }
    }
}

.add-btn-div{
    float: right;
    .userAddBtn{
        background-color: #FFFFFF;
        color: #F9B8A4;
        width: 140px;
        margin-right: 10px;
        border: 2px solid #F9B8A4;
        font-weight: bold;
        outline: none;
    }

    .userAddBtn:hover{
        background-color: #F9B8A4;
        color: white;
    }

    .userDeleteBtn{
        background-color: rgb(214, 25, 25) ;
        width: 140px;
    }

    .userDeleteBtn:hover{
        background-color: rgb(189, 27, 27);
 
    }
}



.dataTables_wrapper{
    margin-top: 50px;
}

.dataTables_wrapper:first-child::after{
    display: block;
}

.modal {
display: none; /* Hidden by default */
position: fixed; /* Stay in place */
z-index: 1; /* Sit on top */
padding-top: 100px; /* Location of the box */
left: 0;
top: 0;
width: 100%; /* Full width */
height: 100%; /* Full height */
overflow: auto; /* Enable scroll if needed */
background-color: rgb(0,0,0); /* Fallback color */
background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
position: relative;
background-color: #fefefe;
margin: auto;
padding: 0;
border: 1px solid #888;
width: 40%;
box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
-webkit-animation-name: animatetop;
-webkit-animation-duration: 0.4s;
animation-name: animatetop;
animation-duration: 0.4s
}

/* Add Animation */
@-webkit-keyframes animatetop {
from {top:-300px; opacity:0} 
to {top:0; opacity:1}
}

@keyframes animatetop {
from {top:-300px; opacity:0}
to {top:0; opacity:1}
}

/* The Close Button */
.close {

    color: black;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color:black;
    text-decoration: none;
    cursor: pointer;
}

.modal-header {
    padding: 2px 16px;
    color: black;
    font-size: 22px;
}

.modal-body {
    padding: 2px 16px;
    display: flex;
    align-items: center;

}

.modal-footer {
    padding: 2px 16px;
    color: white;
}

.show{
    display: block;
}

.footer-btn{
    display: flex;

    .footer-yes{
        background-color: rgb(46, 197, 66);
        width: 80px;
        margin-right: 15px;
    }

    .footer-yes:hover{
        background-color: rgb(23, 136, 38);
    }

    .footer-no{
        background-color: rgb(190, 194, 191);
        width: 80px;
    }

    .footer-no:hover{
        background-color: rgb(136, 138, 136);
    }
}


.multi-select-div{
    height: 62px;
    background-color: white;
    box-shadow: 0 6px 15px #8d8c8c;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 10px 10px;
    border: 1px solid rgba(184, 181, 181, 0.5);
    position: relative;
    display: flex;
    align-items: center;
    margin: 10px 50px;
    // text-align: right;

    p{
        display: inline-block;
        margin: 0 !important;
    }
    
    .minus{
        margin-right: 5px;
    }

    .multi-delete-btn{
        border-radius: 4px;
        width: 80px !important;
        background-color: white;
        color: #F7A085;
        border: 1px solid #F7A085;
        position: absolute;
        right: 10px;
        cursor: pointer;
        outline: none;
    }
}

/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {

    .user-card-container{
   
        flex-direction: column;
        padding: 10px 20px !important;
    }

    .card{
        width: 90% !important;
    }

    .filter-container{
        padding: 10px 20px !important;
    }

    .users-table-container{
        margin: 10px 20px !important;
    }

    .bottom-components{
        display: flex !important;
        justify-content: center !important;
        align-items: center !important;
        padding: 10px 0px !important;

        .component-field{
            padding-left: 10px !important;
            padding-right: 0px !important;
        }
    }
}

/* 
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = B/w 481px to 767px
*/

@media (min-width: 481px) and (max-width: 767px) {

    .user-card-container{
   
        flex-direction: column;
        padding: 10px 20px !important;
    }

    .card{
        width: 90% !important;
    }

    .filter-container{
        padding: 10px 20px !important;
    }

    .users-table-container{
        margin: 10px 20px !important;
    }

    .bottom-components{
        display: flex !important;
        justify-content: center !important;
        align-items: center !important;
        padding: 10px 0px !important;

        .component-field{
            padding-left: 10px !important;
            padding-right: 0px !important;
        }
    }

    

}

